olid #1c6166}.button__reset.third{background-color:#56077c;color:#fffffd;border-right:3px solid #bf16f5;border-bottom:3px solid #bf16f5}@media(max-width: 550px){.button__reset{width:12rem;height:5rem}}.button__equal{grid-column:3/5;width:22rem;height:6rem;display:flex;justify-content:center;align-items:center;background-color:#d13f30;border:none;border-right:3px solid #8f2316;border-bottom:3px solid #8f2316;border-radius:1rem;cursor:pointer}.button__equal.dark{background-color:#d13f30;color:#fffffd;border-right:3px solid #8f2316;border-bottom:3px solid #8f2316}.button__equal.light{background-color:#c85401;color:#fffffd;border-right:3px solid #873b01;border-bottom:3px solid #873b01}.button__equal.third{background-color:#00decf;border-right:3px solid #6cf8ef;border-bottom:3px solid #6cf8ef}@media(max-width: 550px){.button__equal{width:12rem;height:5rem}}
